<template>
  <div>
    <van-nav-bar title="" @click-left="handleLeft" @click-right="handleRight" ref="navbar">
      <template #left>
        {{ cityName }}
        <van-icon name="arrow-down" color="black" />
      </template>
      <template #right>
        <van-icon name="search" size="25" color="black" />
      </template>
    </van-nav-bar>

    <!-- views,components -->
    <!-- <h1>cinema</h1> -->
    <!--  betterScroll  -->
    <div class="box" :style="{
      height: height
    }">
      <cinemas-list :cinemaslist="cinemaList"></cinemas-list>
    </div>
  </div>
</template>

<script>
import cinemasList from '@/components/cinemas/CinemasList.vue'
import BetterScroll from 'better-scroll'
// vue mapState
import { mapState, mapActions, mapMutations } from 'vuex'

export default {
  data() {
    return {
      cinemasList: [],
      height: ''
    }
  },
  /* computed: {
    // vuex
    ...mapState(['cinemaList']) // ,,
  },
  // ,
  computed: mapState(['cinemaList']),

  // 
  computed: {
    cinemaList() {
      return this.$store.state.cinemaList
    } 
  },

  // ,,this.cinemaListthis.$store.state.cinemaList
  */
  computed: mapState(['cinemaList', 'cityId', 'cityName']),
  mounted() {
    // 
    this.height = document.documentElement.clientHeight - document.querySelector('footer').offsetHeight - this.$refs.navbar.$el.offsetHeight + 'px'
    // console.log(this.$refs.navbar);

    // dispatchcinamasgetCinemasDatacityId
    // this.$store.dispatch('getCinemasData', this.$store.state.cityId) //actions
    /* 
    1.this.$store.dispatchactions,.
    2.vuex,actions,,
    3.,store.commitmutations
    4.mutations
    */

    // if,
    if (this.cinemaList.length === 0) {
      this.$store.dispatch('getCinemasData', this.cityId).then(() => {
        // this.$store.dispatch,promise,.then
        // console.log('');
        this.$nextTick(() => {
          new BetterScroll('.box', {
            scrollbar: {
              // 
              fade: true
            }
          })
        })
      })
    } else {
      // console.log('');
      //   // better-scroll,,nextTick
      this.$nextTick(() => {
        new BetterScroll('.box', {
          scrollbar: {
            // 
            fade: true
          }
        })
      })
    }

    // http({
    //   url: `/gateway?cityId=${this.$store.state.cityId}&ticketFlag=1&k=5295042`,
    //   headers: {
    //     'X-Host': 'mall.film-ticket.cinema.list'
    //   }
    // }).then(res => {
    //   // console.log(res.data.data);
    //   this.cinemasList = res.data.data.cinemas

    //   // better-scroll,,nextTick
    //   this.$nextTick(() => {
    //     new BetterScroll('.box', {
    //       scrollbar: {
    //         // 
    //         fade: true
    //       }
    //     })
    //   })
    // })
  },
  methods: {
    // mapActions mapMutations methods
    ...mapActions(['getCinemasData']), // storeactions,,this.getCinemasData
    ...mapMutations(['clearCinemaList']), // storemutations,,this.clearCinemaList

    handleLeft() {
      this.$router.push('/city')

      // cinemaList
      // this.$store.commit('clearCinemaList')
      this.clearCinemaList() //vuex
    },
    handleRight() {
      this.$router.push('/cinemas/search')
    }
  },
  components: {
    cinemasList
  }
}
</script>

<style lang="scss" scoped>
// .box better-scroll
.box {
  // height: 500px;
  overflow: hidden;
  // 
  position: relative;
}
</style>